import { Col, Container, Row } from "react-bootstrap";
import React from "react";
import ConnectButton from "./connect-button";
import uniswap from "../../styles/assets/images/Decentralize_Exchange/uniswap.svg";
import sushiswap from "../../styles/assets/images/Decentralize_Exchange/sushiswap.svg";
import pancakeswap from "../../styles/assets/images/Decentralize_Exchange/pancakeswap.svg";
import layer from "../../styles/assets/images/Decentralize_Exchange/layer.svg";
import dydx from "../../styles/assets/images/Decentralize_Exchange/dydx.svg";
import dodo from "../../styles/assets/images/Decentralize_Exchange/dodo.svg";
import backeryswap from "../../styles/assets/images/Decentralize_Exchange/bakeryswap.svg";
import linch from "../../styles/assets/images/Decentralize_Exchange/1inch.svg";
import mdex from "../../styles/assets/images/Decentralize_Exchange/mdex.svg";

const ServicesStatsOfArt = () => {
  return (
    <Container>
      <Row className="my-5 pb-5 gy-5">
        <Col md={12}>
          <div className="col_flex flex-column gap-5 mt-5">
            <h2 className="text-center">
            Cutting-Edge Decentralized Exchange Script
            </h2>
            <p className="text-center">
              Whether your aim is to enhance an existing cryptocurrency exchange
              platform or establish a decentralized exchange from the ground up,
              our cutting-edge DEX exchange scripts are tailored to support you
              in emulating the success of widely recognized decentralized
              exchanges.
            </p>
          </div>
        </Col>
        <Col lg={4} md={6} col={12} className="col_flex">
          <div className="decenter_cut_card">
            <div className="decenter_cut_img">
              <img src={uniswap} alt="uniswap" />
            </div>
            <div className="decenter_cut_title">UNISWAP</div>
          </div>
        </Col>
        <Col lg={4} md={6} col={12} className="col_flex">
          <div className="decenter_cut_card">
            <div className="decenter_cut_img">
              <img src={backeryswap} alt="bakeryswap" />
            </div>
            <div className="decenter_cut_title">BAKERYSWAP</div>
          </div>
        </Col>
        <Col lg={4} md={6} col={12} className="col_flex">
          <div className="decenter_cut_card">
            <div className="decenter_cut_img">
              <img src={sushiswap} alt="sushiswap" />
            </div>
            <div className="decenter_cut_title">SUSHISWAP</div>
          </div>
        </Col>
        <Col lg={4} md={6} col={12} className="col_flex">
          <div className="decenter_cut_card">
            <div className="decenter_cut_img">
              <img src={pancakeswap} alt="pancakeswap" />
            </div>
            <div className="decenter_cut_title">PANCAKESWAP</div>
          </div>
        </Col>
        <Col lg={4} md={6} col={12} className="col_flex">
          <div className="decenter_cut_card">
            <div className="decenter_cut_img">
              <img src={mdex} alt="mdex" />
            </div>
            <div className="decenter_cut_title">MDEX</div>
          </div>
        </Col>
        <Col lg={4} md={6} col={12} className="col_flex">
          <div className="decenter_cut_card">
            <div className="decenter_cut_img">
              <img src={linch} alt="1inch" />
            </div>
            <div className="decenter_cut_title">1 INCH</div>
          </div>
        </Col>
        <Col lg={4} md={6} col={12} className="col_flex">
          <div className="decenter_cut_card">
            <div className="decenter_cut_img">
              <img src={layer} alt="layer" />
            </div>
            <div className="decenter_cut_title">LAYER</div>
          </div>
        </Col>
        <Col lg={4} md={6} col={12} className="col_flex">
          <div className="decenter_cut_card">
            <div className="decenter_cut_img">
              <img src={dodo} alt="dodo" />
            </div>
            <div className="decenter_cut_title">DODO</div>
          </div>
        </Col>
        <Col lg={4} md={6} col={12} className="col_flex">
          <div className="decenter_cut_card">
            <div className="decenter_cut_img">
              <img src={dydx} alt="dydx" />
            </div>
            <div className="decenter_cut_title">DYDX</div>
          </div>
        </Col>
        <Col md={12}>
        <ConnectButton justifyContentPosition="connect_center" />
        </Col>
      </Row>
    </Container>
  );
};

export default ServicesStatsOfArt;
